import React from "react"

import styled from "styled-components"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import BackgroundParticles from "../../components/backgroundParticle"

export const Button = styled.button`
  background-color: black;
  color: white;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 100px;
  border-radius: 3px;
  opacity: 0.9;
  user-select: none;
  width: 150px;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

  &:focus {
    outline: none;
  }
`
const Link = styled(AniLink)`
  z-index: 3;
`

const ButtonsContainer = styled.div`
  width: 100%;
  z-index: 3;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center;
`

export const Arrow = styled.div`
  transform: rotate(45deg);
  display: inline-block;
  width: 15px;
  height: 15px;
  border-width: 2px 2px 0 0;
  border-style: solid;
  margin-left: ${state => state.margin};
  transition: margin 400ms;
`

const RealtyButton = styled(Button)`
  opacity: 0.6;

  &:hover {
    opacity: 0.6;
  }
`

export const ButtonTextFront = styled.span`
  margin-left: 10px;
  letter-spacing: 3px;
`
export const ButtonTextBack = styled(ButtonTextFront)`
  margin-left: 0;
  margin-right: 10px;
`
export default class MyProjects extends React.Component {
  state = {
    home: "10px",
    me: "0",
    realty: "10px",
  }

  handleClick = name => {
    if (name === this.props.firstName) {
      this.setState({
        home: "45px",
      })
    } else if (name === this.props.secondName) {
      this.setState({
        me: "15px",
      })
    } else {
      this.state.setState({
        realty: "45px",
      })
    }
  }

  render() {
    return (
      <ButtonsContainer>
        <Link style={{ textDecoration: "none" }} duration={0.75} fade to="/">
          <Button onClick={() => this.handleClick(this.props.firstName)}>
            <ButtonTextFront>{this.props.firstName}</ButtonTextFront>
            <Arrow margin={this.state.home} />
          </Button>
        </Link>

        <Link
          style={{ textDecoration: "none" }}
          duration={0.75}
          fade
          to={this.props.to}
        >
          <Button onClick={() => this.handleClick(this.props.secondName)}>
            <ButtonTextFront>{this.props.secondName}</ButtonTextFront>
            <Arrow margin={this.state.me} />
          </Button>
        </Link>

        <Link style={{ textDecoration: "none" }} fade to="">
          <RealtyButton
            onClick={() => this.handleClick(this.props.thirdName)}
            disabled
          >
            <ButtonTextFront>{this.props.thirdName}</ButtonTextFront>
            <Arrow margin={this.state.realty} />
          </RealtyButton>
        </Link>
        <BackgroundParticles />
      </ButtonsContainer>
    )
  }
}
